* {
  font-family: Arial, sans-serif;
}

body {
  background-color: rgb(209, 228, 255);
  padding-bottom: 30px;
}
h1,
.expense-item {
  margin: 10px;
}

h1 {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.expense-item,
.new-expense,
.expenses-filter,
.no-data,
.add-expense {
  margin: 10px auto 10px auto;
  width: 90%;
  max-width: 800px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 30px;
  border-radius: 10px;
}

.add-expense > button {
  margin: auto;
  width: 40%;
  max-width: 200px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: rgb(119, 176, 255);
  color: #fff;
}
.no-data {
  justify-content: center;
}
.new-expense {
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.new-expense > input,
button,
label {
  width: 90%;
  margin: 5px;
  padding: 5px;
}

.new-expense > button {
  padding: 10px;
  border: none;
  color: white;
  background-color: rgb(119, 176, 255);
  border-radius: 3px;
}

#cancel {
  background-color: red;
}
.new-expense > label {
  align-self: flex-start;
  margin-left: 5%;
  margin-bottom: -15px;
}

.expenses-filter {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 10px 20px 20px 30px;
}
.expenses-filter > label {
  width: 50px;
  margin-left: -5px;
  margin-bottom: 0px;
}
.expenses-filter > select {
  padding: 5px;
}
